//a   link    
import { Link } from "react-router-dom";
import "../style/Header.css";
import Light from "../components/light";
import Search from "../components/search";
import "../App.js";

function Header(props) {
  //  class
  const onReset = (e) => {
    e.target.parentElement.parentElement.parentElement.parentElement.firstChild.firstChild.className = "light ";
    e.target.parentElement.parentElement.parentElement.parentElement.firstChild.firstChild.firstChild.className = "light";
  };

  const { isLoggedIn, setIsLoggedIn } = props;
  
  function handleLogout(){
    setIsLoggedIn(false);
  }

  return (
    <div className="header">
      <div className="light-illustration">
        <Light />
      </div>
      
      <div className="menu">
        <div className="menu-top">
          <h1><Link className="menu-title" to={"/"} onClick={onReset}>Light Mall</Link></h1>
          <ul className="menu-help">
          <li>
              {isLoggedIn ? 
              (<Link className="menu-help-item" to={"/"} onClick={handleLogout}></Link>) 
              : (<Link className="menu-help-item" to={"/Login"}> </Link>)}
            </li>
            <li><Link className="menu-help-item" to={"/MyPage"}></Link></li>
            <li><Link className="menu-help-item" to={"/Help"}></Link></li>
          </ul>
        </div>
        
        <div className="menu-bottom">
          <nav>
            <span><Link className="menu-category-item" to={"/Products/"}></Link></span> 
            <span><Link className="menu-category-item" to={"/Products/"}> </Link></span>
            <span><Link className="menu-category-item" to={"/Products/"}> </Link></span>
            <span><Link className="menu-category-item" to={"/Products/"}> </Link></span>
          </nav>

          <Search />
        </div>
      </div>
    </div>
  );
}

export default Header;