//npm install react-router-dom    
import { BrowserRouter, Routes, Route } from "react-router-dom";
// npm i styled-reset  
import { Reset } from "styled-reset";
import { lazy, Suspense, useState } from "react";
import "./style/App.css";
import Header from "./routes/Header.js";

//lazy =    ,       
//   load      { https://react.dev/reference/react/lazy}
const Home = lazy(() => import("./routes/Home.js"));
const Login = lazy(() => import("./routes/Login.js"));
const Help = lazy(() => import("./routes/Help.js"));
const MyPage = lazy(() => import("./routes/MyPage"));
const Products = lazy(() => import("./routes/Products.js"));
const Detail = lazy(() => import("./routes/Detail.js"));
const HelpDetail = lazy(() => import("./routes/HelpDetail.js"));

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false); // 

  const handleLoginSuccess = (success) => {
    setIsLoggedIn(success);
  };
  
  const pages = [
    {
      pageLink: "/",
      view: Home,
    },
    {
      pageLink: "/Login",
      view: Login,
    },
    {
      pageLink: "/Help",
      view: Help,
    },
    {
      pageLink: "/MyPage",
      view: MyPage,
    },
    {
      pageLink: "/Products/:ProductId",
      view: Products,
    },
    {
      pageLink: "/Products/:ProductId/:Detail",
      view: Detail,
    },
    {
      pageLink: "/Help/:id",
      view: HelpDetail,
    },
  ];

  return (
    <BrowserRouter>
      <div className="App">
        <Reset />
        <Header isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} />

        <Suspense fallback={<div />}> {/* loading    fallback */}
          <Routes>
            {/*     return   . */}
            {/* map      key  */}
            {pages.map((page, index) => {
              return (
                <Route 
                  path={page.pageLink} 
                  element={<page.view onLoginSuccess = {handleLoginSuccess}/>} 
                  key={index}
                />
              );
            })}
            </Routes>
        </Suspense>
      </div>
    </BrowserRouter>
  );
}

export default App;
